import React, { useState } from "react";
import QrReader from "react-qr-scanner";

function Scanner() {
  const [actualqrData, setActualQrData] = useState("");
  const [cameraFacingTo, setCameraFacingTo] = useState("rear");

  const handleScan = (result) => {
    if (result) setActualQrData(result);
  };

  const handleError = (err) => console.error(err);

  return (
    <div>
      {actualqrData === "" ? (
        <h3>Scanning...</h3>
      ) : (
        <div>
          <h3>Scanned:</h3>
          <p>{actualqrData.text}</p>
        </div>
      )}

      <QrReader delay="500" onError={handleError} onScan={handleScan} facingMode={cameraFacingTo} />
      <span>Kamera választás: </span>
      <select name="facingmode">
        <option value="front" onClick={(e) => setCameraFacingTo(e.target.value)}>
          Front
        </option>
        <option value="rear" onClick={(e) => setCameraFacingTo(e.target.value)}>
          Rear
        </option>
      </select>
    </div>
  );
}

export default Scanner;
